/* Base Typography - Responsive */
p {
  margin-bottom: 1em;
  line-height: 1.6;
}

h1,
h2,
h3,
h4 {
  margin-bottom: 0.75em;
  color: #E65A88;
  line-height: 1.2;
}

/* Responsive heading sizes */
h1 {
  font-size: clamp(1.75rem, 5vw, 3rem);
}

h2 {
  font-size: clamp(1.5rem, 4vw, 2.25rem);
  scroll-margin-top: 100px;
  /* Match header offset for TOC navigation */
}

h3 {
  font-size: clamp(1.25rem, 3vw, 1.875rem);
  scroll-margin-top: 100px;
  /* Match header offset for TOC navigation */
}

/* Global reset for overflow issues */
html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Custom scrollbar for a more refined look */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #FBF9F7;
}

::-webkit-scrollbar-thumb {
  background: #E65A88;
  border-radius: 4px;
  border: 2px solid #FBF9F7;
}

::-webkit-scrollbar-thumb:hover {
  background: #d94b79;
}

/* Styling for details/summary accordion in FAQ */
details>summary {
  list-style: none;
  cursor: pointer;
}

details>summary::-webkit-details-marker {
  display: none;
}

details[open] summary .accordion-arrow {
  transform: rotate(90deg);
}

/* Subtle fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: fadeIn 0.5s ease-out forwards;
}

/* Fast fade-in for modals */
@keyframes fadeInFast {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fade-in-fast {
  animation: fadeInFast 0.2s ease-out forwards;
}

/* Utility to hide scrollbar but keep functionality */
.scrollbar-hide::-webkit-scrollbar {
  display: none !important;
}

.scrollbar-hide {
  -ms-overflow-style: none !important;
  /* IE and Edge */
  scrollbar-width: none !important;
  /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.no-scrollbar {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

/* Improve touch targets for mobile */
@media (max-width: 768px) {

  button,
  a {
    min-height: 44px;
    min-width: 44px;
  }
}

/* Custom scrollbar for TOC and other scrollable containers */
.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #FBF9F7;
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #E65A88;
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #d94b79;
}

/* Firefox */
.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #E65A88 #FBF9F7;
}

/* Prevent text size adjustment on orientation change */
@media screen and (orientation: landscape) {
  html {
    -webkit-text-size-adjust: 100%;
  }
}
